<template>
  <div class="content">
    <myhead/>
    <dl class="navlist" v-for="(item,index) in this.navdata" :key="index">
      <dt>
        <img :src="item.img" alt="">
      </dt>
      <dd>
        
      </dd>
    </dl>
  </div>
</template>

<script>
  import myhead from '@/components/myhead'
  import mock from '@/mock'
  import axios from 'axios'
  export default {
    data(){
      return {
        navdata:'',
        navid:''
      }
    },
    components:{
      myhead
    },
    mounted(){
      localStorage.navid = this.$route.params.navid;
      this.getJs()
    },
    methods:{
      getJs(){
        this.navid = localStorage.navid || this.$route.params.navid;
        axios.get('/api/navdetail').then(res => {
          for(let key in res.data) {
            if(key === this.$route.params.navid){
              this.navdata = res.data[key]
            }
          } 
        })
      }
    }
  }
</script>

<style scoped>
.content{
  width:100%;
  height:100%;
  overflow-y:scroll;
}
.navlist{
  width:100%;
  height:10rem;
  display:flex;
  justify-content: space-between;
  margin-top:1rem;
}
.navlist img{
  width:10rem;
  height:10rem;
}
</style>


